<template>
  <transition name="toast">
    <div class="toast" :class="position==='bottom'?'positionBottom':''">
      <div class="icon" v-if="icon">
        <!-- <img v-if="icon==='addSuccess'" src="../../image/global/right.png"> -->
        <!-- <img class="waiting" v-if="icon==='addEquipment'" src="../../image/add/Hourglass.png"> -->
      </div>
      <div class="describe" >{{describe}}</div>
    </div>
  </transition>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {}
  },

  mounted () {},

  props: [ 'describe', 'icon', 'position' ],

  components: {},

  computed: {
    ...mapState([])
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../styles/mixin.less';

@keyframes waiting{
  0% {
    transform: rotate(0deg)
  }
  20% {
    transform: rotate(90deg)
  }
  40% {
    transform: rotate(175deg)
  }
  60% {
    transform: rotate(178deg)
  }
  80% {
    transform: rotate(179deg)
  }
  100% {
    transform: rotate(180deg)
  }
}
.toast{
  .wh-l(100%, 94px);
  background: rgba(253, 65, 64, 0.9);
  color: #EEEEEE;
  padding: 5px 0px 0 0px;
  border-radius: 5px;
  .f-center;
  z-index: 99;
  .icon{
    .wh(auto, 22px);
    margin: 5px auto 5px auto;
    position: relative;
    img{
      height: 100%;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-left: -15px;
      margin-top: -11px;
    }
    .waiting{
      margin-left: -10px;
      transform-origin: center;
      animation: waiting .9s ease-out infinite;
    }
  }
  .describe{
    font-size: 30px;
    min-width: 180px;
  }
}
.positionBottom{
  top: 67%;
}
//提示框动画
.toast-enter-active,.toast-leave-active{
  transition: opacity .3s linear;
}
/* 进入开始 */
.toast-enter{
  opacity: 0;
}
/* 出去终点 */
.toast-leave-active{
  opacity: 0;
}

</style>



