      padding: 70px 0 ;
      display: flex;
      justify-content: center;
      img{
        .wh(99px, 94px);
      }
      .night{
        margin-right: 75px;
      }
    }
    .title{
      .wh-l(100%,84px);
      font-size: 50px;
    }
    .button-box{
      .wh-l(100%, 123px);
      display: flex;
      justify-content: center;
      align-items: center;
      .button{
        .wh-l(190px, 62px);
        font-size: 24px;
        background: #15AF67;
        border-radius: 10px;
        color: #ffffff;
      }
    }
    .night{
      .button-box {
        .wh-l(418px, 64px);
        display: flex;
        justify-content: space-bettwen;
        align-items: center;
        border: 2px solid #13ae6a;
        border-radius: 10px;
        margin: 0 auto;
        margin-top: 40px;
        span {
          font-size: 28px;
          color: #13ae6a;
          width: 50%;
        }
        span:first-child {
          border-right: 1px solid #13ae6a;
        }
        .active {
          .wh(50%, 60px);
          background: #b6e6d0;
        }
        .button {
          .wh-l(190px, 62px);
          font-size: 24px;
          background: #15af67;
          border-radius: 10px;
          color: #ffffff;
        }
      }
      .button-box{
        margin-top: 22px;
      }
      .set-number {
        .wh(100%, 334px);
        display: flex;
        margin: 60px 0 0px 0;
        background:#f8f8f8;
        padding: 0;
        padding-top: 56px;
      }
      .h-260{
        height: 260px !important;
        padding: 70px 0;
        background: none;
      }
    }
    .button-top{
      .wh-l(618px, 64px);
      display:flex;
      border: 2px solid #13ae6a;
      border-radius: 10px;
      margin: 0 auto;
      margin-top: 37px;
      .active {
        background: #b6e6d0;
      }
      .tabone{
        flex: 1;
        display: flex;
        justify-content: center;
        img{
          .wh(64px, 30px);
          align-self: center;
