    .done{
      position: absolute;
      bottom: 84px;
      .wh(100%, 188px);
      img{
        .wh(188px, 188px);
        display: inline-block;
      }
    }
  }
  .common_alert{
    .alert_icon{
      font-size: 33px;
      font-weight: 600;
      color: #585858;
      margin-top: 72px;
    }
    .alert_describe{
      font-size: 28px;
