    }
    .alert_operation{
     font-size: 32px;
     color: white;
     display: flex;
     position: absolute;
     bottom: 0;
     width: 100%;
    }
    .determine_one{
      .wh-l(50%,110px);
      &:nth-child(1) {
        background:#cccccc;
